<template>
    <div class="group-list">
        <div v-if="$route.name === '群发列表'">
            <Input style="width: 200px;margin-right: 30px;" size="large" type="text" v-model="name" placeholder="请输入公号名"></Input>
            <Button size="large" type="primary" @click="search">搜索</Button>
            <Table class="group-table" stripe :columns="groupHeader" :data="groupData"></Table>
            <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      groupData: [],
      dataList: [],
      groupHeader: [
        {
          key: "code",
          title: "code",
          align: "center",
          width: 80
        },
        {
          key: "name",
          title: "公号名称",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$axios.get('/material', {params: {code: params.row.code}})
                      .then(res => {
                        that.$Message.info(res.data.success)
                      })
                    }
                  }
                },
                "同步素材"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "发送消息",
                        query: { code: params.row.code }
                      });
                    }
                  }
                },
                "群发消息"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "info",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "群发记录",
                        query: { code: params.row.code }
                      });
                    }
                  }
                },
                "群发记录"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
  },
  methods: {
    search() {
      let name = this.name.trim()
      if(name != "") {
        this.$axios.get("/conf/find_one", {params: {name: this.name}})
        .then(res => {
          if(res.data.data.length != 0) {
            this.groupData = res.data.data
          } else {
            this.showDataList()
            this.$Message.info("没有查询到包含该字符的公号，请检查后重新输入")
          }
          this.name = ""
        })
      } else {
        this.$Message.info("公号名不能为空")
      }
    },
    showDataList() {
      this.$axios.get("/conf").then(res => {
        let arr = res.data.data;
        for(let i = 0; i < arr.length; i ++){
            if(arr[i].status == 1) {
                this.dataList.push(arr[i])
            }
        }
        this.groupData = this.dataList.slice(0, 10)
      });
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.groupData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.group-table {
  width: 100%;
  clear: both;
  margin: 30px 0;
}
</style>
